  color: #999;
  font-size: 14px
}

.section-title .more {
  position: absolute;
  display: block;
  width: 50px;
  right: 0;
  top: 20px;
  padding-right: 15px;
  text-align: right
}

.section-title .more i {
  display: inline-block;
  width: 14px;
  height: 14px;
  background-image: url("data:image/png;base64,");
  background-size: 100% auto;
  background-repeat: no-repeat
}

.section-content {
  position: relative;
  overflow: hidden;
  zoom: 1
}

.section-content:after {
  content: "";
  display: table;
  clear: both
}

.shops {
  background-color: #fff;
  margin-bottom: 10px;
  font-size: 0;
  position: relative
}

.shops:after {
  content: " ";
  display: block;
  padding-top: 58%
}

.shops:before {
  z-index: 1;
  content: "";
  box-sizing: border-box;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(.5);
  transform: scale(.5);
  -webkit-transform-origin: left top;
  transform-origin: left top;
  pointer-events: none;
  border-top: 1px solid #e5e5e5
}

.shop-wrap,
.shops:before {
  position: absolute;
  top: 0;
  left: 0
}

.shop-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  overflow: hidden
}

.shop-item {
  position: relative;
  display: inline-block;
  width: 33.333%
}

.shop-item:before {
  z-index: 1;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(.5);
  transform: scale(.5);
  -webkit-transform-origin: left top;
  transform-origin: left top;
  pointer-events: none;
  border-right: 1px solid #e5e5e5
}

.shop-item:last-child:before {
  content: "";
  display: none
}

.shop-item img {
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  max-width: 100%;
  max-height: 100%
}

.channels {
  margin-bottom: 12px;
  position: relative;
  overflow: hidden
}

.channels:after {
  display: block;
  padding-top: 54%;
  content: " "
}

.channels .primary-channel {
  left: 0;
  width: 55%;
  padding-right: 5px
}

.channels .primary-channel,
.channels .secondary-channel {
  position: absolute;
  top: 0;
  height: 100%;
  box-sizing: border-box
}

.channels .secondary-channel {
  right: 0;
  width: 45%
}

.channels .secondary-channel a {
  height: 50%
}

.channels .secondary-channel a:first-child {
  margin-bottom: 5px
}

.channels .secondary-channel a:last-child {
  margin-top: 5px
}

.channels a {
  display: block;
  position: relative;
  height: 100%
}

.channels img {
  position: absolute;
  width: 100%;
  height: 100%
}

.view-history {
  display: block;
  height: 40px;
  margin: 0 10px 20px;
  color: #fe6573;
  text-align: center;
  line-height: 40px;
  position: relative
}

.view-history:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(.5);
  transform: scale(.5);
  -webkit-transform-origin: left top;
  transform-origin: left top;
  border: 1px solid #fe6573
}

.hot-goods {
  overflow: hidden
}

.hot-goods ul {
  position: relative;
  padding: 0 6px;
  zoom: 1
}

.hot-goods ul:after {
  content: "";
  display: table;
  clear: both
}

.hot-goods li {
  float: left;
  width: 50%;
  box-sizing: border-box;
  padding-bottom: 6px
}

.hot-goods li:nth-child(odd) {
  padding-right: 3px
}

.hot-goods li:nth-child(2n) {
  padding-left: 3px
}

.hot-goods li .goods-item .thumb {
  margin-bottom: 10px;
  background-color: #fff
}

.hot-goods li .goods-item .detail {
  padding: 0 8px 1px
}

.hot-goods li .goods-item .title {
  color: #666;
  padding-bottom: 0;
  line-height: 16px;
  height: 32px;
  margin-bottom: 10px;
  white-space: normal;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical
}

.daily-title {
  display: none;
  position: relative;
  padding: 6px 10px;
  border-bottom: none;
  font-size: 14px;
  height: 22px;
  line-height: 22px;
  overflow: hidden;
  background-color: transparent
}

.daily-title .pull-left {
  position: relative;
  padding-left: 28px
}

.daily-title .pull-right {
  position: relative;
  padding-right: 12px
}

.daily-title .home-more {
  display: block;
  width: 50px;
  text-align: right;
  font-size: 12px;
  color: #909090
}

.daily-title .today .icon-calendar,
.daily-title .today .week {
  color: #ff6366
}

.daily-title .today .icon-calendar {
  background-image: url("data:image/png;base64,")
}

.daily-title .icon-calendar,
.daily-title .week {
  color: #999
}

.icon-calendar {
  left: 0;
  top: -1px;
  width: 22px;
  height: 22px;
  box-sizing: border-box;
  font-size: 12px;
  text-align: center;
  line-height: 26px;
  background-image: url("data:image/png;base64,")
}

.icon-calendar,
.icon-home-more {
  position: absolute;
  background-size: 100% auto;
  display: inline-block;
  background-repeat: no-repeat
}

.icon-home-more {
  right: 0;
  top: 6px;
  width: 5px;
  height: 9px;
  background-image: url("data:image/png;base64,")
}

.line-block .lineblock-title .lineblock-font {
  background-color: #f4f4f4
}

.line-block.line-block-gray {
  position: relative;
  margin: 20px 10px 10px
}

.line-block.line-block-gray:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(.5);
  transform: scale(.5);
  -webkit-transform-origin: left top;
  transform-origin: left top;
  pointer-events: none;
  border-top: 1px solid #ababab;
  height: 1px;
  margin: 7px 0 0
}

.line-block .lineblock-title {
  position: relative
}

.category-guid {
  font-size: 0;
  margin-bottom: 10px
}

.list-finished,
.notice {
  padding: 10px
}

.notice {
  display: block;
  font-size: 12px;
  line-height: 16px;
  color: #f90;
  background-color: #ffc;
  letter-spacing: .4px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden
}

.activity {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 10px;
  overflow: hidden;
  font-size: 0;
  position: relative
}

.activity:after {
  content: " ";
  display: block;
  padding-top: 40%
}

.activity .item {
  width: 50%
}

.activity .item:first-child {
  position: relative
}

.activity .item:first-child:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(.5);
  transform: scale(.5);
  -webkit-transform-origin: left top;
  transform-origin: left top;
  pointer-events: none;
  border-right: 1px solid #e5e5e5
}

.fadeIn {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-name: a;
  animation-name: a
}

@-webkit-keyframes a {
  0% {
    opacity: 0
  }
  to {
    opacity: 1
  }
}

@keyframes a {
  0% {
    opacity: 0
  }
  to {
    opacity: 1
  }
}

	
</style>


